@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}


.cover-container {
    min-width: 100%;
    min-height: 100vh;
    background: linear-gradient(180deg,#0f2027 60%, #203a43, #2c5364);
    
    display: flex;
    justify-content: space-between;
}

.cover-video {
    max-height: 100vh;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}
.cover-title {
    color: #B2BABB;
    padding-top: 14rem;
    text-align: center;
    width: inherit;
    position: absolute;
}

.servicioTecnico {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding-right: 64px;
}

.Info{
    color: white;
    text-align: center;
}

@media screen and (min-width: 300px) and (max-width: 1100px) {
    .cover-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }
    .cover-video {
        position: unset;
        justify-content: center;
        width: 100%;
    }
    .servicioTecnico {
        padding-top: 38px;
        width: 100%;
        align-items: center;
        padding-right: unset;
        padding-left: 18px;
    }
    .cover-title {
        padding-top: unset;
    }
    .Info{
        padding-bottom: 28px;
    }
}

#ServicioTecnico {
    /* width: 100%; */
    display: flex;
    align-items: center;
    flex-direction: row;
} 

@media screen and (max-width: 700px) {
    #ServicioTecnico {
        flex-direction: column;
        margin: 20px;
    }    
}
#Tramites-title { 
  color: black;
  padding-top: 5.5rem;
  text-align: center;
}
#Tramites-cards {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding-top: 2.6rem;
}
.carousel__container {
  margin-top: 1rem;
  padding: 3rem 3rem 2.5rem 3rem;
  overflow: auto;
  width: 100%;
  margin-bottom: 1rem;
  white-space: nowrap;
}
.carousel__container::-webkit-scrollbar {
  height: 1rem; 
}
.carousel__container::-webkit-scrollbar-thumb {
  width: 2rem;
  background: #A3E4D7;
  border-radius: 0.5rem;
}
.carousel__container:hover .carousel__container-item {
  opacity: 0.9;
}
.carousel__container:hover .carousel__container-item:hover {
  opacity: 1;
  transform: scale(1.3);
}
.carousel__container-item {
  width: 13rem;
  height: 6rem;
  border: 2px solid #96DBF1;
  border-radius: 25px;
  display: inline-block;
  margin-right: 10px;
  transform-origin: center left;
  transition: 450ms all;
  overflow: hidden;
  cursor: pointer;
}
.carousel__container-item:hover ~ .carousel__container-item{
  transform: translate3d(90px, 0, 0);
}
.carousel__container-item__img {
  width: 13rem;
  height: 6.5rem;
  object-fit: cover;
}
#servicios {
  padding: 2rem;
}
#servicios h1 {
  text-align: center;
  margin-top: 7rem;
  margin-bottom: 2rem;
}
.mapa {
  border-radius: 10%;
}

@media only screen and (min-width: 700px) and (max-width: 1180px) {
  #Tramites-title {
    padding-top: 9rem;
  }
  #Tramites-cards {
    padding-top: 6rem;
    padding-bottom: 3rem;
  }
}

  
  
.Card-item {
    width: 236px;
    height: 340px;
    border: 2px solid black;
    background: linear-gradient(#2C5364, #203A43, #0F2027); 
    border-radius: 16px;
    text-align: center;
    padding-top: 2rem;
    margin-bottom: .7rem;
}

.Card-title {
    /* text-align: center; */
    text-decoration: none;
    color: white; 
    font-weight: bold;
}
 
.Card-links {
    font-size: 1.3rem;
    text-decoration: none;
    color: aquamarine;
}

.Card-links a { 
    color: inherit; 
} 
#Contacto {
  width: 100%; 
  height: 100vh; 
  color: white;
  background: linear-gradient(180deg,#0f2027 15%, #203a43, #2c5364);
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

#Contacto-map {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}


#Contacto-network_icons {
  display: flex;
  align-items: center;
  justify-content: center;
}

#Contacto-network_icons img{
  width: 2rem;
  height: 2rem;
}


#Nav-container {
    max-width: 100%;
}

#NavContainer{
    padding-left: 5.8rem;
    max-width: 100%;
}
#NavLinks {
    text-decoration: inherit;
    color: aquamarine;
    padding: auto;

}

#toggleButton {
    background: grey;
}

#NavLinks-container {
    margin-right: 1rem; 
}
